@import "../../app.scss";

.MuiGrid-item {
  text-align: center;
}

.navbar {
  width: 100%;
  color: white;
  font-size: 14px;
  position: fixed;
  top: 0;
  z-index: 1;
  background: linear-gradient(to top, transparent 0%, rgb(0, 0, 0, 0.3) 50%);

  &.scrolled {
    // background-color: var(--main-color);
    width: auto;
  }
  .links {
    .link {
      padding: 0px 5px;
      p {
        white-space: nowrap;
      }
    }
  }

  .createButton {
    button {
      color: white;
      padding: 5px 10px;
      margin: 2px;
      border-radius: 0.5em;
      background-color: #ff3588;
      border: none;
    }
  }
  .usernameInfo {
    .logout {
      button {
        padding: 5px 10px;
        background-color: #6c29e9;
        color: white;
        border: none;
        border-radius: 0.5em;
      }
    }
  }
  .container {
    padding: 0px 50px;
    // display: flex;
    // align-items: center;
    // justify-content: space-between;
    height: 70px;

    .left {
      display: flex;
      align-items: center;

      img {
        height: 25px;
        margin-right: 40px;

        @include mobile {
          display: none;
        }
      }

      span {
        margin-right: 20px;
        cursor: pointer;

        @include tablet {
          display: none;
        }

        &.navbarmainLinks {
          @include tablet {
            display: block;
          }
        }
      }
    }

    .right {
      display: flex;
      align-items: center;

      .icon {
        margin: 0px 15px;
        cursor: pointer;
      }

      .profile {
        .options {
          display: none;
          background-color: var(--main-color);
          border-radius: 5px;
        }

        span {
          padding: 10px;
          cursor: pointer;
        }

        &:hover {
          .options {
            display: flex;
            flex-direction: column;
            position: absolute;
          }
        }
      }
    }
  }
}
